* {
  box-sizing: border-box;
  border: none;
  outline: none;
}

body {
  background-color: #000;
  font-size: 16px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}

@media screen and (min-width: 2000px) {
  .container {
    width: 2000px;
  }
}

.search-form {
  justify-content: center;
  align-items: center;
  margin: 30px auto;
  display: flex;
}

.search-input {
  width: 35%;
  text-align: center;
  color: #fff;
  background-color: #000;
  border: 1px solid pink;
  border-radius: 10px;
  padding: 10px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: auto;
  display: flex;
}

.photo-card {
  width: 280px;
  height: 240px;
  cursor: pointer;
  z-index: 0;
  cursor: pointer;
  transition: box-shadow .5s, transform .5s;
  box-shadow: 0 0 1px 1px #000;
}

.photo {
  object-fit: cover;
  max-width: 100%;
  aspect-ratio: 2 / 1.3;
  z-index: 0;
  transition: box-shadow .5s, transform .5s;
  display: block;
  position: relative;
}

.photo:hover {
  z-index: 1;
  transform: perspective(400px)scale(120%)rotateY(15deg);
  box-shadow: 0 0 24px 5px #000;
}

.info {
  justify-content: space-around;
  font-size: 10px;
  line-height: .5;
  display: flex;
}

.info-item {
  color: #fff;
  flex-direction: column;
  align-items: center;
  display: flex;
}

